export default {
  name: 'goodsDetail',
  props: {
    goodsData: {
      type: Object
    },
    detailCurrentIndex:{
      type:Number
    },
    detailParentIndex:{
      type:Number
    }
  },
  data() {
    return {};
  },
  methods: {
    plus(e) {
      this.$emit('plus',{
        parentIndex:this.detailParentIndex,
        currentIndex:this.detailCurrentIndex,
        goodsId:this.goodsData.id,
        isNoAttr:true
      },e);
    },
    reduce(e) {
      this.$emit('reduce',{
        parentIndex:this.detailParentIndex,
        currentIndex:this.detailCurrentIndex,
        goodsId:this.goodsData.id,
      },e);
    },
    maskHandle() {
      this.$emit('maskHandle');
    }
  },
  mounted() {
    console.log(this.goodsData);
  }
};




