export default {
  props: {
    parentIndex: {
      type: Number,
      require: true
    },
    currentIndex: {
      type: Number,
      require: true
    },
    baseurl: {
      type: String,
      default: ''
    },
    item: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {};
  },
  methods: {
    plus(e) {
      this.$emit('plus',{
        parentIndex:this.parentIndex,
        currentIndex:this.currentIndex,
        goodsId:this.item.id,
        isNoAttr: true,
      },e);
    },
    reduce(e) {
      this.$emit('reduce',{
        parentIndex:this.parentIndex,
        currentIndex:this.currentIndex,
        goodsId:this.item.id,
      },e);
    },
    choose(e){
      console.log('xxx');
      this.$emit('choose',{
        parentIndex:this.parentIndex,
        currentIndex:this.currentIndex,
        goodsId:this.item.id
      })
    }
  },
  mounted() {

  }
};




