export default {
  name: 'mj-tab-panel',
  data() {
    return {
      isCurrent: false
    };
  },
  props: {
    label: String,
    active: {
      type: [Boolean, String]
    },
    tabkey: [String, Number]
  },
  computed: {
    classes() {
      return this.$parent.activeIndex === this._uid || this.isCurrent ? 'mj-tab-active' : '';
    }
  },
  watch: {
    active() {
      this.$parent.init();
    },
    label() {
      this.$parent.init();
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$parent.init();
    });
  }
};




