export default {
  props: {
    lable: {
      type: String,
      default: '选好了'
    },
    total: {
      type: Number
    },
    totalMoney: {
      type: Number
    }
  },
  data() {
    return {};
  },
  computed:{
    totalPrice(){
      return this.totalMoney.toFixed(2);
    }
  },
  methods: {
    handleClickBtn() {
      this.$emit('handleClickBtn');
    },
    initTrolleyPos() {
      this.$emit('initTrolleyPos', this.$refs.footer.offsetHeight);
    },
    handleClickTrolley() {
      this.$emit('handleClickTrolley');
    }
  },
  mounted() {
    // console.log(this.$parent.);
    this.initTrolleyPos();
  }
};




